.header{
    margin-top:50px;
    padding-bottom: -10px;
}

.headerTitles{
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: 'Lora', serif;
    color: #444444;
}

.headerTitleSm{
    position: absolute;
    top: 13%;
    font-size: 20px;
}
.headerTitleLg{
    position: absolute;
    top: 15%;
    font-size: 100px;
}

.headerImg{
    max-width: 100%;
    max-height: 650px;
    width: 100vw;
    height: 100vw;
    margin-top: 50px;
    object-fit: cover;
    object-position: left 80%;
}