.skills{
    width:100%;
    display: flex;
    flex-direction: column;
    align-items:stretch;
    justify-content: center;
    background-color: black;
}

.title{
    font-size:30px;
    padding-top: 30px;
    height:100px;
    color: white;
}

.content{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    padding-bottom: 30px;
    flex-wrap: wrap;
}

.contentItem{
    width:120px;
    height:160px;
    border-radius: 10px;
    background-color: #2a2929;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 10px;
}

.itemTitle{
    color: white;
}