.footBar{
    display: flex;
    background-color: #2a2929;
    width: 100%;
    height: 80px;
    justify-content: flex-end;
    align-items: center;
}

.footBarRight{
    margin-right: 30px;
    display:flex;
    align-items: center;
    justify-content: center;
    position:relative;
}

.icon{
    margin-right: 15px;
}

#emailDiv{
    position:relative;   
}
#emailDiv #email{
    visibility: hidden;
    position: absolute;
    left:-50px;
    bottom: 50px;
}

#emailDiv:hover #email{
    visibility: visible;
    background-color: black;
    color:white;
}

#phoneDiv{
    position:relative;   
}
#phoneDiv #phone{
    visibility: hidden;
    position: absolute;
    width:140px;
    left:-40px;
    bottom: 50px;
}

#phoneDiv:hover #phone{
    visibility: visible;
    background-color: black;
    color:white;
}

.insDiv img{
    visibility: hidden;
    width:140px;
    height:140px;
    position: absolute;
    right:10px;
    bottom:50px;
    z-index: 2;
}
.insDiv:hover img{
    visibility: visible;
}

@media screen and (max-width: 960px) {
    .footBar{
        display: flex;
        background-color: #2a2929;
        width: 100%;
        height: 80px;
        justify-content: center;
        align-items: center;
    }
    
    .footBarRight{
        margin-right: 0px;
    }
    
    .icon{
        margin-right: 15px;
    }
}