
  .verticalTimeLine{
    background: #e9d5a1;
    font-family: "Montserrat", sans-serif;
    font-size: 16px;
    color: rgb(53, 53, 53);    
  }
  .title {
    font-size: 15em;
    text-align: center;
    font-family: "Bebas Neue", sans-serif;
  }
  
  h3 {
    padding-top: 0.25em;
  }
  
  .vertical-timeline-element-content {
    box-shadow: 0 0.25em 0.5em 0 rgba(0, 0, 0, 0.25),
      0 0.4em 1.25em 0 rgba(0, 0, 0, 0.15) !important;
    padding: 2em 3em !important;
  }
  
  .date {
    color: rgb(201, 251, 255);
  }
  
  #description {
    margin: 1.5em 0 2em 0;
  }
  
  .button {
    text-decoration: none;
    padding: 0.5em 1em;
    border-radius: 5px;
    color: white;
  }
  
  .workButton {
    background-color: #06d6a0;
  }
  
  .workButton:hover {
    background-color: #0ac593;
  }
  
  .schoolButton {
    background-color: #f9c74f;
  }
  
  .schoolButton:hover {
    background-color: #f3bc3c;
  }

  .bgDetailProject{
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 1000;
  }

  .detailProject{
    background-color: white;
    color:black;
    width:50%;
    height:80vh;
    left:25%;
    top:10%;
    opacity: 1;
    position:absolute;
    border-radius: 10px;
    display:flex;
    flex-direction: column;
    align-items: center;
    padding: 10px;
  }

  .detailProject>img{
    width:80%;
    height:200px;
    object-fit:contain;
    flex:5
  }

  .closeButton{
    margin-right: 10px;
    flex:1;
    align-self: end;
  }
  
  .popUpText{
    flex:4;
    margin-top: 20px;
    width:82%;
    overflow: auto;
  }

  @media only screen and (max-width: 1700px) {
    .vertical-timeline-element-date {
      display: block !important;
      float: none !important;
      color: rgb(44, 44, 44);
      margin-top: 1.5em;
    }
  }